import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet';

// import classNames from 'classnames';

import Block from 'components/atoms/Block';
import Strong from 'components/atoms/Strong';
import Heading from 'components/atoms/Heading';
import Svg from 'components/atoms/Svg';
import Paragraph from 'components/atoms/Paragraph';

import AudioRecorder from 'components/organismes/AudioRecorder';

import { getTranslations } from 'modules/systemData/selectors';

import isAuthenticated from 'utils/isAuthenticated';
import isVerified from 'utils/isVerified';

import styles from './DashboardPage.scss';

class DashboardPage extends PureComponent {
  render() {
    const { translations } = this.props;

    return (
      <React.Fragment>
        {/* eslint-disable react/jsx-curly-brace-presence */}
        <Helmet>
          <title>{translations.dashboardPageTitle}</title>
        </Helmet>
        {/* eslint-enable react/jsx-curly-brace-presence */}
        <Block className={styles.wrapper}>
          <br />
          <Heading type="h1">Front-end test</Heading>

          <Block className={styles.note}>
            <Svg type="note" className={styles.noteIcon} />
            Note: you have 5 minutes for each question to answer
          </Block>

          <Heading type="h3">Question 1</Heading>
          <Paragraph>
            What is a potential pitfall with using <Strong>typeof bar === {'"object"'}</Strong> to determine if bar is
            an object? How can this pitfall be avoided?
          </Paragraph>
          <AudioRecorder />
        </Block>
      </React.Fragment>
    );
  }
}

DashboardPage.propTypes = {
  translations: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  translations: getTranslations(state),
});

export default isAuthenticated(isVerified(connect(mapStateToProps)(DashboardPage)));



